import { createStore } from 'vuex';
import counter from "./counter";
import axiosConfig from '@/apis/axiosConfig';

export default createStore({
  state: {
    userId:"",
    authToken:""
  },
  getters: {
    getUserId(state, getters, rootState, rootGetters){
      return state.userId;
    },
    getAuthToken(state, getters, rootState, rootGetters){
      return state.authToken;
    }
  },
  mutations: {
    setUserId(state,payload){
      state.userId = payload;
    },
    setAuthToken(state,payload){
      state.authToken = payload;
    }
  },
  actions: {
    //playload : {userId:"xxx", duration:3000} 이런식으로 저장되어 있음
    setUserIdByAsync(context, playload){
      console.log("context: " , context);

      new Promise((resolve, reject) => {
        //시간을 요하는 작업을 진행(주로 Back-end로 데이터 요청)
        setTimeout(()=> {
          //성공적으로 응답을 얻었을 경우
          console.log(playload.userId);
          resolve(playload.userId);
        }, playload.duration);
      })
      //resolve가 되었을 경우, mutation을 이용해서 상태값을 변경
      .then((data) => {
        context.commit("setUserId", data);
        console.log("userId 상태 변경 완료")
      })
      //예외가 발생했거나, reject된 경우
      .catch((error) =>{
        console.log("userId 상태 변경 실패")
      });
    },

    //로그인이 성공했을 때 실행
    saveAuth(context, payload){
      context.commit("setUserId", payload.userId);
      context.commit("setAuthToken", payload.authToken);
      sessionStorage.setItem("userId", payload.userId);
      sessionStorage.setItem("authToken", payload.authToken);
      axiosConfig.addAuthHeader(payload.authToken); //앞으로 모든 요청시에 인증토큰을 헤더에 실어서 보낼거양!
    },

    //로그아웃할 때 실행
    deleteAuth(context, payload){
      context.commit("setUserId", "");
      context.commit("setAuthToken", "");
      sessionStorage.removeItem("userId");
      sessionStorage.removeItem("authToken");
      axiosConfig.removeAuthHeader(); //헤더에서 인증 토큰을 지울거양!
    },
    loadAuth(context, payload){
      context.commit("setUserId", sessionStorage.getItem("userId") || "");
      context.commit("setAuthToken", sessionStorage.getItem("authToken") || "");
      if(context.state.authToken !== ""){
        axiosConfig.addAuthHeader(context.state.authToken);
      }
    }
  },
  modules: {
    counter
  }
})

